body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 15px;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.title {
  color: #333;
}

.breed-select {
  cursor: pointer;
  width: 20%;
  color: #333;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 20px;
  margin-bottom: 40px;
  padding: 10px;
  font-size: 16px;
}

.loader_style {
  height: 50px;
  width: 50px;
  animation: rotate 1s infinite;
}

.loader_style:before, .loader_style:after {
  content: "";
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: block;
}

.loader_style:before {
  background-color: #fff;
  margin-bottom: 10px;
  animation: ball1 1s infinite;
  box-shadow: 30px 0 #ff3d00;
}

.loader_style:after {
  background-color: #ff3d00;
  animation: ball2 1s infinite;
  box-shadow: 30px 0 #fff;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg)scale(.8);
  }

  50% {
    transform: rotate(360deg)scale(1.2);
  }

  100% {
    transform: rotate(720deg)scale(.8);
  }
}

@keyframes ball1 {
  0% {
    box-shadow: 30px 0 #ff3d00;
  }

  50% {
    margin-bottom: 0;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #ff3d00;
  }

  100% {
    margin-bottom: 10px;
    box-shadow: 30px 0 #ff3d00;
  }
}

@keyframes ball2 {
  0% {
    box-shadow: 30px 0 #fff;
  }

  50% {
    margin-top: -20px;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #fff;
  }

  100% {
    margin-top: 0;
    box-shadow: 30px 0 #fff;
  }
}

.loader, .loader_style {
  display: none;
}

.loader {
  text-align: center;
  color: #0757f8 #2003fa;
  margin: 20px 0;
  font-weight: bold;
}

.cat-info {
  gap: 20px;
  margin: 10px auto 0;
}

.image {
  max-width: 600px;
  border: 1px solid #0b0a0a;
  border-radius: 20px;
  margin-bottom: 5px;
}

.name {
  color: #333;
  margin-bottom: 5px;
  font-size: 24px;
}

.description {
  color: #555;
  max-width: 600px;
  margin-bottom: 5px;
}

.temperament {
  color: #333;
}

.error {
  color: red;
  font-size: 22px;
}

